.imageBox img {
  width: 50%;
  padding: 0;
  margin: 0;
}

@media screen and (min-width: 800px) {
  .imageBox img {
    width: 60%;
    padding: 0;
    margin: 0;
  }
  .container {
    width: 100vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
